.gallery {
  &__slider {
    border-radius: $radius;
    overflow: hidden;
    margin-bottom: rem(10); }
  &__img {
    background-color: $c-text;
    @include cover;
    padding-top: percentage(370 / 690); }
  &__thumb {
    background-color: $c-text;
    @include cover;
    border-radius: $radius;
    padding-top: 15%; } }
