.nav {
  $this: &;
  font-size: em(22);
  font-weight: 600;
  color: $black;
  ul {
    display: flex;
    align-items: center;
    justify-content: space-between; }
  a {
    @include tr(color .3s);
    @include hover {
      @extend #{$this}__current-item; } }
  &__current-item {
    color: $accent; }

  &--footer {
    font-size: inherit;
    font-weight: inherit;
    color: inherit;
    ul {
      display: block; }
    li {
      @include notlast {
        margin-bottom: rem(10); } } } }

